.skills-container {
  text-align: center;
  padding: 50px;
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
}

.skill-badge {
  background-color: #007bff;
  color: white;
  padding: 10px 15px;
  border-radius: 5px;
  font-weight: bold;
}